<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          fab
          dark
          color="rgba(39,55,70,1)"
          fixed
          right
          bottom
          v-bind="attrs"
          v-on="on"
          @click="newForm()"
        >
          <v-icon size="23" color="#D5DBDB" dark>add</v-icon>
        </v-btn>
      </template>
      <v-card color="#F2F4F4">
        <v-card class="overflow-hidden">
          <v-app-bar
            style="z-index: 20001 !important"
            absolute
            dark
            color="primary"
            elevate-on-scroll
            scroll-target="#scrolling-techniques-7"
          >
            <v-btn icon dark @click="dialog = false">
              <v-icon>arrow_back</v-icon>
            </v-btn>
            <v-toolbar-title>{{ textDialog }}</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn dark text @click="action()"> {{ textSave }} </v-btn>
            </v-toolbar-items>
          </v-app-bar>
          <v-sheet
            id="scrolling-techniques-7"
            class="overflow-y-auto"
            height="100vh"
            style="color: #273746"
          >
            <v-container>
              <v-card-title class="text-h5" style="color: #273746">
                Nueva Area
              </v-card-title>
              <v-card-text style="color: #273746">Datos del Area</v-card-text>

              <v-form ref="form">
                <v-text-field
                  class="text-capitalize"
                  outlined
                  disabled
                  v-model="reference"
                  label="Referencia"
                  required
                ></v-text-field>
                <v-text-field
                  outlined
                  v-model="location"
                  label="Area"
                  required
                ></v-text-field>

                <!-- AQui va el componente,... -->
                <!-- Recordar... -->
                <!-- Nuevo punto específico -->
                <item-area :template="result" />
                <!-- 
               -->
              </v-form>
            </v-container>
          </v-sheet>
        </v-card>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  components: {
    ItemArea: () => import("@/components/item/ItemArea.vue"),
  },
  name: "AreaNew",
  data() {
    return {
      editDialog: false,
      //nom: "200-11",
      //description: "Polaridad de las conexiones",
      value: null,
      reference: 1,
      nomenclature: "",
      location: "",
      dialog: false,
      nameEdit: "areaList",
      result: [],
    };
  },
  computed: {
    ...mapState({
      data: (state) => state.customers.findCustomer,
    }),
    items() {
      return this.data.areaList.data;
    },
    config() {
      return this.data.areaList.config;
    },
    textDialog() {
      if (this.editDialog) return "Editar Area";
      return "Nueva Area";
    },
    textSave() {
      if (this.editDialog) return "Guardar";
      return "Crear";
    },
  },
  methods: {
    ...mapActions({
      getItem: "customers/search",
      saveItem: "customers/save",
    }),
    //Funciones de soporte..
    action() {
      if (this.editDialog) {
        this.update();
      } else {
        this.create();
      }
    },
    //asignar datos...
    toAssign(data) {
      //Asignar para poder visualizar datos guardados.
      this.reference = data.reference;
      this.nomenclature = data.nomenclature;
      this.location = data.location;
    },
    //Vaciar variables..
    emptyData() {
      this.nomenclature = "";
      this.location = "";
      this.value = null;
    },
    //Configuración para abrir o crear nuevo formulario..
    newForm() {
      console.log("hola");
      this.emptyData();
      this.editDialog = false;
      this.reference = this.items.length + 1;
      this.result = JSON.parse(JSON.stringify(this.config));
    },
    openForm(data) {
      console.log("se abre..");
      this.editDialog = true;
      this.dialog = true;
      console.log(data);
      this.toAssign(data);
      this.result = data.result;
    },
    //Acciones de Actualizar y Crear...
    update() {
      const newDataUpdate = this.data[this.nameEdit].data.map((a) =>
        a.reference == this.reference
          ? {
              ...a,
              //nomenclature: this.nomenclature,
              location: this.location,
              result: this.result,
            }
          : a
      );
      this.data[this.nameEdit].data = newDataUpdate;
      this.saveItem(this.data);
      this.dialog = false;
      this.emptyData();
    },
    create() {
      const dt = {
        reference: this.reference,
        location: this.location,
        result: this.result,
        //nomenclature: this.nomenclature,
      };
      this.data[this.nameEdit].data.push(dt);
      this.saveItem(this.data);
      this.emptyData();
      this.dialog = false;
      //console.log(dt);
    },
  },
};
</script>

<style scoped>
.code input {
  text-transform: uppercase;
}
</style>